import React ,{useState,useEffect,useRef} from "react";
import "./index.css";


function CalDesign()
{
  const [result,setResult]=useState("");
   const inputRef=useRef(null);
   useEffect(()=>inputRef.current.focus());

     function handleClick(e)
     {
     setResult(result.concat(e.target.name));

     }
     function backSpace()
     {
         setResult(result.slice(0,-1));
     }
     function clear()
     {
         setResult("");
     }
     
      function calculate()
      {
          try{
              setResult(eval(result).toString());
          }catch(Error)
          {
          setResult("Error");
          }
      }
    return(
        <>

<h1 style={{textAlign:"center",fontSize:"40px",color:"tomato"}}>React Calculator Using WPA</h1>  
         <div className="Calc-app">

            
              
                  <input type="text" value={result} ref={inputRef}/>
              
              <div className="keypad">
                  <button onClick={clear} id="clear">Clear</button>
                  <button  onClick={backSpace} id="backspace">Backspace</button>
                  <button  name="+"  onClick={handleClick}>+</button>
                  <button  name="7"  onClick={handleClick}>7</button>
                  <button  name="8"  onClick={handleClick}>8</button>
                  <button  name="9"  onClick={handleClick}>9</button>
                  <button  name="-"  onClick={handleClick}>-</button>
                  <button  name="4"  onClick={handleClick}>4</button>
                  <button  name="5"  onClick={handleClick}>5</button>
                  <button  name="6"  onClick={handleClick}>6</button>
                  <button  name="*"  onClick={handleClick}>&times;</button>
                  <button  name="1"  onClick={handleClick}>1</button>
                  <button  name="2"  onClick={handleClick}>2</button>
                  <button  name="3"  onClick={handleClick}>3</button>
                  <button  name="/"  onClick={handleClick}>/</button>
                  <button  name="0"  onClick={handleClick}>0</button>
                  <button  name="."  onClick={handleClick}>.</button>
                  
                  
                  <button  onClick={calculate} id="result">Result</button>

              </div>

         </div>
        
    
        </>
    );
}

export default CalDesign;